:root {
  --background: #ffffff;
  --foreground: #000000;
  --surface: #fafafa;
  --surface-elevated: #ffffff;
  --muted: #6b6b6b;
  --border: #e5e5e5;
  --toast-bg: #ffffff;
  --toast-color: #000000;
  --toast-border: #e5e5e5;
}
.dark {
  --background: #000000;
  --foreground: #ffffff;
  --surface: #0a0a0a;
  --surface-elevated: #0f0f0f;
  --muted: #a0a0a0;
  --border: #1a1a1a;
  --toast-bg: #0f0f0f;
  --toast-color: #ffffff;
  --toast-border: #1a1a1a;
}
html {
  scroll-behavior: smooth;
}
body {
  background-color: var(--background);
  color: var(--foreground);
  font-family: var(--font-inter), -apple-system, BlinkMacSystemFont, system-ui, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.font-serif {
  font-family: var(--font-playfair), 'Georgia', 'Times New Roman', serif;
}
*:focus-visible {
  outline: 2px solid var(--foreground);
  outline-offset: 2px;
}
input:focus-visible,
button:focus-visible {
  outline: none;
  box-shadow: 0 0 0 2px var(--background), 0 0 0 4px var(--foreground);
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: transparent;
}
::-webkit-scrollbar-thumb {
  background: var(--border);
  border-radius: 9999px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--muted);
}
html {
  scrollbar-width: thin;
  scrollbar-color: var(--border) transparent;
}
::selection {
  background-color: rgba(0, 0, 0, 0.1);
  color: inherit;
}
.dark ::selection {
  background-color: rgba(255, 255, 255, 0.15);
}
.underline-offset-4 {
  text-underline-offset: 4px;
}
.hover-lift {
  transition: transform 200ms ease;
}
.hover-lift:hover {
  transform: translateY(-2px);
}
h1,
h2,
h3 {
  text-wrap: balance;
}
html {
  overflow-y: scroll;
}
